<template>
<div class="home" key="home">
	<div class="container" style="margin-top:86px">
		<div id="carouselExampleControls" class="carousel slide" data-ride="carousel">

			<div class="carousel-inner">
				<div class="carousel-item" v-for="(item,index) in select" :key="index" :class="item.active">
					<router-link :to="item.addr">
					<img class="d-block w-100" :src="item.img">
					<div class="carousel-caption d-block">
						<h4>{{item.title}}</h4>
					</div>
					</router-link>
				</div>
			</div>

			<a class="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
				<span class="carousel-control-prev-icon" aria-hidden="true"></span>
				<span class="sr-only">Previous</span>
			</a>
			<a class="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
				<span class="carousel-control-next-icon" aria-hidden="true"></span>
				<span class="sr-only">Next</span>
			</a>
		</div>
		<hr>
	</div>

	

	<div class="container mt-3" style="display:flex!important;justify-content:center;line-height:2em">	
		<div>
			<a @click="change('');addCurrent('1')" class="mr-4 inline-block" :class="{selected:current==1}">全部</a>
			<a @click="change('badge-success');addCurrent('2')" class="mr-4 inline-block" :class="{selected:current==2}">万能茶馆</a>
			<a @click="change('badge-info');addCurrent('3')" class="mr-4 inline-block" :class="{selected:current==3}" >万能旅馆</a>
			<a @click="change('badge-danger');addCurrent('4')" class="mr-4 inline-block" :class="{selected:current==4}" >万能影馆</a>
			<router-link to="/about" class="mr-4 inline-block">关于</router-link>
		</div>
	</div>

	<div id="arti" class="container pt-1">
		<hr>
		<div class="row">
			<div class="col-12" v-for="(article,index) in arti" :key="index" :class="`animation-${index+1}`">
				<router-link :to="article.addr">
				<div class="card mt-4" v-if="filter==''||filter==article.color">
					<div class="row" style="margin:0">
					<img class="card-img home col-12 col-md-6" :src="article.img">
					<div class="card-body col-12 col-md-6">
						<div class="inside">
						<p class="badge badge-pill mb-3" :class="article.color">{{article.type}}</p>
						<h5 class="card-title home" :title="article.title">{{article.title}}</h5>
						<p class="card-text home">{{article.subtitle}}</p>
						</div>
					</div>
				</div>
				</div>
				</router-link>
			</div>

			<div class="col-12 mt-5" v-pre>
				<center><h5 class="subtext">—— · ——</h5></center>
			</div>
		</div>
	</div>

</div>
</template>

<script>
import axios from 'axios'
export default {
	name: 'home',
	data(){
		return{
			arti:[],
			select:[],
			filter:[],
			current:1
		}
	},
	created () {
		document.title = '萬能蓬萊茶館';
	},
	mounted(){
		axios
		.get('./docs/list.json')
		.then(response => {
			this.arti=response.data.arti;
			this.select=response.data.select;
		})	
	},
	methods:{
		change(index){
			this.filter = index;
		},
		addCurrent(index){
			this.current = index
		}
	}
}

</script>



<style lang="scss" scoped>
$grid-columns: 6; 

@for $i from 1 through $grid-columns {
	$time: ($i * 100+300) + ms;
	.animation-#{$i} {
		transition: all 0.5s linear;
		animation-name: toTop; // toBottom
		animation-duration: 0.5s; // 注释掉 会没有动画 就是帕帕一帧一帧的出来
		animation-fill-mode: both;
		animation-delay: $time;
	}
}

// 方向 下-->上
@keyframes toTop {
	0% {
		opacity: 0;
		transform: translate3d(0, 100%, 0);
	}

	100% {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}
} 
